import React from "react"
import Container from '../../containers/QiMen'

const Home = ({ chartType, chart, onClick, initialDate, heading, handleSearch, onDateChanged, getChart, onSquarePressed }) => {

  if(chart && chart.chart) {
    chart.structure = chart.structure.replace('F', 'Yin').replace('M', 'Yang')
  }
  return <>
    <Container chartType={chartType} chart={chart} onClick={onClick} initialDate={initialDate} onSquarePressed={onSquarePressed} handleSearch={handleSearch} onDateChanged={onDateChanged} />
  </>
}

export default Home
